@use "mixins";

.contact {
  margin-bottom: 20px;

  .title {
    @include mixins.sub-header;
  }

  .contact-item {
    display: flex;
    align-items: center;

    svg {
      width: 18px;
      display: inline-block;
      color: var(--fg-color);
    }

    a {
      text-decoration: none;
      padding-top: 4px;
      padding-left: 4px;
      @include mixins.body-text;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
